import React, { Component } from 'react';
import {Logout , CheckSession} from '../state/actions';
import {connect} from 'react-redux';



class User extends Component {
  
  

   sendToLogout(){
    this.props.Logout()
    this.props.CheckSession()
  }

  render() {
    if(this.props.role == "user"){
      return (
        <div className="container"> 
          <div className="menu">
            <div className="row">
              <div className="col-4">
                <h2>VacationHunter</h2>
              </div>
              <div className="col-4">
                <h3>Hello Username</h3>
              </div>
              <div className="col-4">
                <button onClick={this.sendToLogout.bind(this)} className="btn btn-primary">Logout</button>
              </div>
            </div>
          </div>  
        </div>
      );
    }
  }
}

const mapStateToProps = state => { 
  return { role: state.role };
}; 

const  mapDispatchToProps = dispatch => {  
  return  { 
      Logout: function() { 
        return  dispatch(Logout());
        },
        CheckSession: function(){
          return dispatch(CheckSession())
        }
      }
  };
const user = connect(mapStateToProps, mapDispatchToProps)(User);

export default user;
